.btn {
  $this: &;

  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  border: 2px solid  $accent;
  background-color: $accent;
  color: $white;

  font-size: em(18);
  font-weight: 700;

  padding: rem(10) rem(25);
  min-width: 230px;
  @include tr(background-color .3s, border-color .3s, color .3s);

  @include hover {
    &:not(#{$this}--no-hover) {
      background-color: transparent;
      color: $accent;
      border-color: $accent; } }

  @include xs {
    min-width: 0;
    width: 100%; }

  &__icon {
    margin-right: .8em;
    font-size: 1.2em; }

  &--md {
    min-width: 255px; }

  &--transp-white {
    border-color: $white;
    background-color: transparent; }

  &--full {
    min-width: 0;
    width: 100%; } }

.delete-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  background-color: #ededed;
  color: #dc3232;

  width: rem(20);
  height: rem(20);
  border-radius: 50%;

  > span {
    @include icon-close(.7rem, 1px, currentColor);
    display: block; } }

.btn-sm {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  background-color: $lighten;
  border: 1px solid $c-border;

  font-size: em(12);

  min-width: rem(120);
  padding: rem(9) rem(10); }
