.card {
  $this: &;

  display: flex;
  flex-direction: column;

  height: 100%;
  position: relative;
  box-shadow: 0px 0px 10px 0px rgba(1, 1, 1, 0.15);

  &:hover {
    #{$this}__btn {
      max-height: rem(55); } }

  &__top {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: $white;
    padding: rem(15) rem(20);
    @include tr(background-color .3s);

    @include lg {
      padding: rem(15); } }

  &__img {
    @include cover;
    background-color: $light;
    display: block;
    padding-top: percentage(280 / 330); }

  &__subttl {
    font-size: em(14);
    color: #8493a9; }

  &__block {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: rem(20); }

  &__btn {
    display: block;
    max-height: 0;
    overflow: hidden;
    @include tr(max-height .4s); }

  &__text {
    margin-bottom: rem(25);
    display: block; }

  &--lg {
    color: $white;
    &:hover {
      #{$this}__top {
        background-color: $accent; } }

    #{$this}__top {
      background-color: $c-text; }

    #{$this}__subttl {
      color: #bbc5cc; }

    #{$this}__img {
      position: relative;
      padding-top: 100%;
      @include before {
        background-image: linear-gradient(to top, rgb(56,62,71) 0%, rgba(56,62,71,0) 100%);
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        top: 50%;
        z-index: 0; } }

    #{$this}__text {
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 5;
      -webkit-box-orient: vertical;
      height: 6.6rem;
      overflow: hidden;
      @include tr(height .3s);

      @include lg {
        // -webkit-line-clamp: 3
 } } }        // height: 4.2rem

  &--center {
    @include hover {
      #{$this}__btn {
        @include visible; } }

    #{$this}__block {
      @include vcenter;
      bottom: auto;
      width: 100%;
      text-align: center; }

    #{$this}__btn {
      max-height: none;
      @include tr(visibility .3s, opacity .3s);
      @include hidden; } } }

.card-news {
  display: flex;
  flex-direction: column;

  height: 100%;

  @include tr(box-shadow .3s);

  @include hover {
    @include shadow; }

  &__img {
    @include cover;
    background-color: $dark;

    display: block;
    padding-top: percentage(320 / 405); }

  &__content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    background-color: $white;
    padding: rem(30) rem(35); }

  &__title {
    display: block;
    margin-bottom: rem(15); }

  &__text {
    margin-bottom: rem(20);
    display: block;
    flex-grow: 1;

    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    height: 6.6rem;
    overflow: hidden; } }
