html {
  font-size: $base-fz + px;
  line-height: 1.4;
  color: $c-text;
  @include reg;
  @include sm {
    font-size: $base-fz-sm + px; } }

body {
  background-color: $white;
  min-width: 320px;
  @include no-scroll; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.logo {
  display: inline-block;
  font-size: 0; }

.main {
  background-color: $lightest;
  &--white {
    background-color: $white; } }

.container {
  max-width: none;
  @include rmin(1625) {
    max-width: 1440px; }
  @include rmin(1400) {
    max-width: 1230px; }
  @include rmin(1200) {
    max-width: 1030px; }

  &--lg {
    max-width: none !important;
    margin-left: 0;
    margin-right: 0;
    padding-left: $container--lg-padding;
    padding-right: $container--lg-padding;

    @include lg {
      padding-left: $container-padding;
      padding-right: $container-padding; } } }

.page-inner {
  max-width: 1650px; }

.word {
  text-transform: uppercase;
  font-weight: 800;
  font-size: em(254);
  line-height: .8;
  opacity: .1;
  white-space: nowrap;
  @include lg {
    font-size: em(220); }
  @include md {
    font-size: em(180); }
  @include sm {
    font-size: em(150); }
  @include xs {
    font-size: em(120); } }

.number-lg {
  color: $accent;
  font-weight: 800;
  font-size: em(260);
  line-height: .8;
  text-transform: uppercase;
  @include lg {
    font-size: em(220); }
  @include md {
    font-size: em(180); }
  @include sm {
    font-size: em(150); }
  @include xs {
    font-size: em(120); } }

.img-block {
  @include cover;
  background-color: $dark;
  color: $white;

  padding-top: percentage(270 / 1920);
  margin-bottom: rem(35);

  &__block {
    background-color: $dark;
    padding: rem(75) rem(65);

    max-width: rem(855);
    position: relative;
    bottom: rem(-35); }

  &__title {
    margin-bottom: rem(15); }

  &__text {
    font-size: em(18); } }

.title-under {
  border-bottom: 1px solid $lighten;
  padding-bottom: rem(25);
  margin-bottom: rem(25); }

.price {
  font-size: 1rem;
  line-height: .9;
  strong {
    font-size: rem(32); }

  &--sm {
    strong {
      font-size: rem(21); } } }

.back-link {
  color: $accent;
  display: inline-flex;
  align-items: center;
  @include hover {
    text-decoration: underline; }
  &__icon {
    margin-right: .5em; } }

.price-info {
  font-size: 12px;
  strong {
    font-size: 1rem; } }

.line-imgs {
  display: flex;
  flex-wrap: wrap;

  &__img {
    @include cover;
    background-color: $dark;

    width: percentage(1 / 6);
    padding-top: 17%; } }

.col {
  &--2-10 {
    width: percentage(2 / 10); } }

