.cabinet {
  background-color: $white;

  &__hero {
    background-color: $dark;
    color: $white; }

  &__breadcrumbs {
    margin-bottom: rem(30); }

  &__hero-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-top: rem(15);
    padding-bottom: rem(15); }

  &__hero-title {
    margin-bottom: rem(25); }

  &__block {
    @include notlast {
      margin-bottom: rem(45); } } }

.cabinet-meta {
  display: flex;
  flex-wrap: wrap;

  &__left, &__right {
    width: percentage(690 / 1650); }

  &__right {
    margin-left: percentage(75 / 1650); }

  &__order-item {
    border-bottom: 1px solid  $lighten;
    padding-top: rem(15);
    padding-bottom: rem(15); } }

.cabinet-meta-block {
  $this: &;

  &__title {}


  &__block {
    display: flex;
    flex-wrap: wrap;
    @include notlast {
      margin-bottom: rem(25); } }

  &__block-title {
    width: percentage(180 / 690); }

  &__block-meta {
    width: percentage((690 - 180) / 690); }

  &--block-bdb {
    #{$this}__block-meta {
      border-bottom: 1px solid  $lighten; } } }



