.feature {
  $this: &;
  text-align: center;
  &__icon {
    margin-bottom: rem(20);
    margin-left: auto;
    margin-right: auto;
    .icon {
      font-size: rem(25); } }

  &__title {
    margin-bottom: rem(10); }

  &__text {
    a {
      @include hover {
        text-decoration: underline; } } }

  &--left {
    text-align: left;
    #{$this}__icon {
      margin-left: 0;
      margin-right: 0; } } }


